.header {
  padding: 5rem 0;
  position: relative;
}

@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-color: #000;
  opacity: 0.6;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@media (min-width: 480px) {
  .header {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
.header .contenido-header {
  position: relative;
}

.header .contenido-header h1 {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}
.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  color: #fff;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #c39c63;
}

.nav-principal a.activo {
  color: #c39c63;
}

.barra {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .logo {
  width: 50%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
  }
}
